import { apiBaseUrl } from './config.js'
import * as common from './common.js'
import * as db from './db.js' //common

const methodsToken = [
	'login/doLoginByUsername',
	'login/loadUserInfo',
	'login/doSlientLogin',
	'login/doLogin',
	'login/logout',
	'sample/teamLeader/taskList',
	'sample/getSampleItemsForCollectUser',
	'sample/auditSampleItem',
	'sample/getSampleAuditRecordBySampItemId'
]

const uploadFile = (url, filePath, success) => {
	let userToken = db.get("userToken") || uni.getStorage('TOKEN') || '';
	uni.uploadFile({
		url: apiBaseUrl + url,
		filePath:filePath,
		name:'files',
		header: {
		'Content-Type': 'multipart/form-data;',
		'satoken': userToken
		},
		success: res => {
			typeof success === "function" && success(JSON.parse(res.data));
		},
	})
};

const post = (method, data, callback, complete, fail) => {
	// uni.showLoading({
	// 	title: '...'
	// })
	let userToken = db.get("userToken") || uni.getStorage('TOKEN') || '';
	debugger
	if(methodsToken.indexOf(method) >= 0 && method!=="login/doLoginByUsername" ) {
		if(!userToken || userToken === '') {
			common.jumpToLogin();
			return false;
		} 
	}
	
	uni.request({
		url: apiBaseUrl + method,
		data: data,
		header: {
			'Accept': 'application/json',
			'Content-Type': 'application/json',
			'satoken': userToken
		},
		method:'POST',
		success: (response) => {
			uni.hideLoading();
			const result = response.data
			if(!response.statusCode !== 200) {
				if(result.code === 401 || result.code === 403) {
					db.del("userToken");
					out()
					uni.showToast({
						title: result.message,
						icon: 'none',
						duration: 1000,
						complete: function() {
							setTimeout(function() {
								uni.hideToast();
								let current =  getCurrentPages()
								current = current[current.length - 1]
								if (current.route.indexOf('/pages/index/index') > -1 ) {
									return
								}
								uni.navigateTo({
									url: '/pages/index/index'
								})
							}, 1000)
						}
					})
				} 
			}
			callback(result);
		},
		complete: (response) => {
			setTimeout(function() {
				uni.hideLoading();
			}, 1000)
			complete?complete(): "";
		},
		fail: (error) => {
			uni.showLoading({
				title: ''
			});
			uni.hideLoading();
			if(error && error.data) {
				showError(error.data)
			}
		}
	})
}

const get = (url,  callback) => {
	// uni.showLoading({
	// 	title: '...'
	// });
	let userToken = db.get("userToken") || uni.getStorage('TOKEN')  || '';
	uni.request({
		url: apiBaseUrl + url,
		header: {
			'Accept': 'application/json',
			'Content-Type': 'application/json',
			'satoken': userToken
		},
		method: 'GET',
		success: (response) => {
			uni.hideLoading();
			const result = response.data
			
			debugger
			if(result.code === 401 || result.code === 403){
				out()
				db.del("userToken");
				uni.showToast({
					title: result.message,
					icon: 'none',
					duration: 1000,
					complete: function() {
						setTimeout(function() {
							uni.hideToast();
							let current =  getCurrentPages()
							current = current[current.length - 1]
							if (current.route.indexOf('/pages/index/index') > -1 ) {
								return
							}
							uni.navigateTo({
								url: '/pages/index/index'
							})
						}, 1000)
					}
				})
			} 
			callback(result);
		},
		fail: (error) => {
			uni.hideLoading();
			if (error && error.response) {
				showError(error.response);
			}
		},
		complete: () => {
			setTimeout(function() {
				uni.hideLoading();
			}, 250);
		}
	})
}

const getParam = (url, params, callback) => {
	// uni.showLoading({
	// 	title: '...'
	// });
	debugger
	let userToken = db.get("userToken") || uni.getStorage('TOKEN') || '';
	uni.request({
		url: apiBaseUrl + url,
		header: {
			'Accept': 'application/json',
			'Content-Type': 'application/json',
			'satoken': userToken
		},
		data: params,
		method: 'GET',
		success: (response) => {
			uni.hideLoading();
			const result = response.data
			if(response.statusCode === 200) {
				if(result.code === 401) {
					out()
					uni.showToast({
						title: result.message,
						icon: 'none',
						duration: 1000,
						complete: function() {
							setTimeout(function() {
								uni.hideToast();
								let current =  getCurrentPages()
								current = current[current.length - 1]
								if (current.route.indexOf('/pages/index/index') > -1 ) {
									return
								}
								uni.navigateTo({
									url: '/pages/index/index'
								})
							}, 1000)
						}
					})
				} 
			}
			callback(result);
		},
		fail: (error) => {
			uni.hideLoading();
			debugger
			if (error && error.response) {
				showError(error.response);
			}
			this.$common.errorToShow(error)
		},
		complete: () => {
			setTimeout(function() {
				uni.hideLoading();
			}, 250);
		}
	})
}

const showError = error => {
	let errorMsg = ''
	switch (error.status) {
		case 400:
			errorMsg = ''
			break
		case 401:
			errorMsg = ''
			break
		case 403:
			errorMsg = ''
			break
		case 404:
			errorMsg = `: ${error.config.url}`
			break
		case 408:
			errorMsg = ''
			break
		case 500:
			errorMsg = ''
			break
		case 501:
			errorMsg = ''
			break
		case 502:
			errorMsg = ''
			break
		case 503:
			errorMsg = ''
			break
		case 504:
			errorMsg = ''
			break
		case 505:
			errorMsg = 'HTTP'
			break
		default:
			errorMsg = error.message
			break
	};
	
	uni.showToast({
		title: errorMsg,
		icon: 'none',
		duration: 1000,
		complete: function() {
			setTimeout(function() {
				uni.hideToast();
			}, 1000);
		}
	});
}

